.app_wrapper {
  
}

.app__starting {
  padding: 10px ;
}

.app {
  margin: 0 auto;
  display: grid;
  width: 80vw;
  height: 100vh;
  grid-template-areas: "n c";
  grid-template-columns: 2fr 10fr;
}

.errorsBlock {
  position: fixed;
  z-index: 100;
  right: 20px;
  top: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.mainPage_content {
  grid-area: c;
  background-color: rgb(160, 218, 241);
  padding: 10px;
}

.toTop {
  position: fixed;
  left: 83%;
  top: 190px;
  font-size: 18px;
  cursor: pointer;
  color: rgb(41, 94, 170);
  z-index: 10;
}

.visible {
  display: flex;
}

.hidden {
  display: none;
  ;
}

.toTop img {
  height: 20px;
  margin-right: 2px;
}