.header {
    display: flex;
    background-color: rgb(75, 158, 236);
    height: 45px;
}

.headerWrapper {
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
}

.headerLogo {
    padding-left: 10px;
    display: flex;
}

.logoImage {
    height: 35px;
    border-radius: 50%;
    background-color: white;
}

.login {
    display: flex;
    align-items: center;
    margin-right: 25px;
}

.loginImage {
    height: 35px;
    margin: 0;
    margin-left: 5px;
}

.login a {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
}

.login a:hover {
    color: rgba(251, 255, 20, 0.952);
}

.loggedInWraper {
    position: relative;
}

.loggedIn {
    display: flex;
    align-items: center;
    background-color: #a0d3e8;
    padding: 0 10px;
    border-radius: 5px;
    margin: 0 10px;
    cursor: pointer;
}

.loggedIn img {
    height: 30px;
}

.loggedIn span {
    text-decoration: none;
    color: white;
    line-height: 30px;
}

.loggedIn__click {
    background-color: rgb(126, 189, 231);
}

.loggedIn:hover {
    background-color: rgb(126, 189, 231);
}

.logOut {
    position: absolute;
    top: 37px;
    right: 10px;
    cursor: pointer;
    background-color: #4b9eec;
    padding: 6px 12px;
    border-radius: 5px;
    border: 1px solid #a0daf1;
    color: white;
}

.logOut::after {
    content: '\f08b';
    font-family: FontAwesome;
    margin-left: 10px;
    color: red;
}

.logOut:hover {
    color: rgba(251, 255, 20, 0.952);
}

.logOut__hide {
    display: none;
}